import { mapState } from "vuex";

export default {
    data() {
        return {
            list: {},
            name: '',
            open_date: '',
            step_num: 1000,
            bail_currency: 1.00,
            setting: null,
            template_activity: null,
            join: false
        }
    },
    computed: {
        ...mapState({
            stepImg: state => state.mallConfig.plugin.step,
            userInfo: state => state.user.info,
        })
    },
    methods: {
        getSetting() {
            let that = this;
            that.$request({
                url: that.$api.step.setting,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.setting = response.data;
                    that.template_activity = response.data.template_activity;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList(id) {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.step.activity_detail,
                data:{
                    activity_id: id
                },
            }).then(response=>{
                that.getSetting();
                if(response.code == 0) {
                    that.list = response.data.list;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        subscribe() {
            this.$subscribe(this.template_activity).then(res => {
                this.apply();
            }).catch(res => {
                this.apply();
            });
        },
        apply() {
            let that = this;
            let id = that.list.id;
            uni.showLoading({
                mask: true,
                title: '报名中...'
            });
            that.$request({
                url: that.$api.step.activity_join,
                data: {
                    activity_id: id
                },
                method: 'POST',
            }).then(response => {
                uni.hideLoading();
                if (response.code == 0) {
                    let date = that.list.begin_at;
                    uni.redirectTo({
                        url: '/plugins/step/dare/dare?join=1&date='+date
                    })
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.getList(options.id);
    }
}
